import React from 'react'
import SharingsIndexTemplate from 'intl/SharingsIndexTemplate'
import { graphql } from 'gatsby'

const lang = {
  'sharings': '信息分享'
}

const SharingsIndexPage = ({ data }) => (
  <SharingsIndexTemplate intl={lang} sharings={data.allMarkdownRemark.edges}/>
)

export default SharingsIndexPage

export const pageQuery = graphql`
  query {
    allMarkdownRemark(
      sort: { order: DESC, fields: [ frontmatter___date ] },
      filter: { frontmatter: { lang: { eq: "zh-Hant" }, type: { eq: "sharing" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            path
            title
            addToIndex
          }
        }
      }
    }
  }
`
