import React from 'react'
import Layout from 'components/Layout'
import SharingLink from 'components/SharingLink'

export default function SharingsIndexTemplate({ intl, sharings }) {
  const Sharings = sharings
    .filter(sharing => sharing.node.frontmatter.addToIndex)
    .map(sharing => <li key={sharing.node.id}><SharingLink key={sharing.node.id} sharing={sharing.node} /></li>)

  return (
    <Layout fullMenu>
      <article id="main">
        <header>
          <h2>{intl['sharings']}</h2>
        </header>
        <section className="wrapper style5">
          <div className="inner">
            <h3>{intl['sharings']}</h3>
            <ul>
              {Sharings}
            </ul>
          </div>
        </section>
      </article>
    </Layout>
  )
}
